.grid {
    inline-size: 90%;
    margin-inline: auto;
    max-inline-size: 64em;
}

/* helpers/sticky.css */

.sticky {
    position: sticky;
    will-change: transform;
}

.sticky--top {
    top: 0;
}

/* layout/base.css */

* {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    line-height: 1.75;
    margin: 0;
    min-block-size: 100vh;
}

/* layout/header.css */

.header {
    background-color: #fff;
    padding-block: 1em;
}


.header::after {
    bottom: 0;
    box-shadow: 0 0.0625em 0.5em rgba(0, 0, 0, 0.3);
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity 0.3s;
    z-index: -1;
}

.header.is-active::after {
    opacity: 1;
}

.navigation__logo{
    font-weight: 700;
    font-size: 30px;
}

.navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navigation__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.navigation__list--inline {
    display: flex;
    gap: 1.5em;
}

.navigation__link {
    color: inherit;
    display: block;
    padding-inline: 1em;
    text-decoration: none;
    font-size: 18px;
}

.navigation__link--is-active {
    background-color: #000;
    border-radius: 999em;
    color: #fff;
}
